/* @import url("./smallScreen.css"); */

*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
}
body{
    margin: 0;
    padding: 0;
    background-color: #ede6e6;
}
.header{
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    padding: 0px 4px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
    height: 80px;
    color: white;
    background-color: #fefefe;

}

.left,.right{
    display: flex;
    align-items: center;
}
.left .logo{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

nav ul{
    display: flex;
}
nav ul li{
    list-style-type: none;
    margin-right: 10px;
    font-size: 18px;
}
nav ul li a{
    text-decoration: none;
    color: white;
    font-size: 20px;
}
.left a{
    font-size: 24px;
    color: black;
    font-weight: bold;
    text-decoration: none;
}
.right .menu{
    display: none;
    /* visibility: hidden; */
}
.left img{
    height: 80px;
    width: 80px;
    margin-right: 10px;
    border-radius: 50%;
    

}
.small-screen{
    display: none;
}
.large-screen{
    display: block;
}

/* Hero section */

.hero{
    display: flex;
    margin-top: 80px;
    flex-direction: column;
    align-items: center;
    height: 80vh;
    color: #fff;
    justify-content: center;
    background:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url("../photos/bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
.hero p{
    margin-top: 10px;
}
.hero-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.hero-text a{
    text-decoration: none;
    color: white;
    font-size: 20px;
    
}

.button{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(7, 56, 236, 0.681);
    margin-top: 10px;
    width: fit-content;
    padding: 6px 4px;
    height: 40px;
    border-radius: 5px;
}

/* My electronic card */



.footer{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10vh;
    color: white;
    background-color: #333;
}

.formation{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
    margin:10px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
    width:fit-content;
    height: fit-content;
    color: #fefefe;
    padding: 4px 20px ;
    background-color: #333;
}
.formation h3{
    margin: 10px 0px;
}
.join-us{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    background-color: rgba(7, 56, 236, 0.681);

}

.formation li{
    list-style: circle;
    font-size: 18px;
}
.formation li::marker{
    font-weight: bolder;
    font-size: 20px;
}
.join-us .arrow{
    font-weight: bolder;
    font-size: 22px;
    margin-left: 6px;
}
.formation a{
    text-decoration: none;
    color: #fefefe;
    font-size: 20px;
}

.right{
    background-color: rgba(7, 56, 236, 0.681);
    padding: 10px;
    color: white;
    border-radius: 0px;
}

.page-not-found {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 80vh;
    margin: 140px auto 0px auto;
}
.page-not-found .continue{
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 34px;
    width: fit-content;
    padding: 10px;
    background-color: #222;
    border-radius: 6px;
}
.continue a{
    text-decoration: none;
    color: #fff;
}


