@media screen  and (max-width:768px){
    .header{
        flex-direction: column;
        height: fit-content;
        padding: 10px 0px;
        width: 100%;
    }
    .right{
        margin: 20px 0px 0px 0px;
    }
    .right ul li a {
        font-size: 16px;
    }
    .logo a{
        font-size: 30px;
    }
    .formation{
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto;
        width: 100%;
    }
    }