

.blog-layout{
    margin-top: 160px;
}
.blog-nav-bar{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0px;
}
.blog-nav-bar ul{
    display: flex;
}
.blog-nav-bar ul li{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 3px;
    border-radius: 6px;
    margin-right: 10px;
    list-style: none;
}
.blog-nav-bar ul li:hover{
    background-color: rgba(7, 56, 236, 0.681);
}
.blog-nav-bar li a:hover{
    color: white;
}
.blog-nav-bar ul li a{
    text-decoration: none;
    color: black;
}

@media screen  and (max-width:768px){
    .blog-nav-bar ul li a{
        font-size: 14px;
    }
    }


.my_blog{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    margin: auto;
    height: 80vh;
}