.services{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 160px;
}
.services-items{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.services h2{
    margin: 10px 0px;
    text-align: center;
}
.join-us{
    margin: 10px;
}

.service-card{
    height: fit-content;
    padding: 4px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1);
    width: 240px;
    border-radius: 5px;
    margin: 10px;
    background-color: #fefefe;
}
.service-card img{
    height: 120px;
    width: 100%;
    border-radius: 5px;
}

.service-card a{
    display: block;
    color: black;
    text-decoration: none;
}
.service-card h3{
    font-size: 16px;
    margin-bottom: 10px;
}

.service-card .card-info{
    margin-top: 10px;
}
.service-card .card-description a{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    color: white;
    background-color: rgba(7, 56, 236, 0.681);
    width: 100px;
    padding: 6px;
    border-radius: 5px;
    margin: 10px 0px;
}
.price{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 2px;
}
.gray{
    color: gray;
}
.price h2{
    font-size: 16px;
}

.services-Detail{
    margin-top: 180px;
    width: 100%;
    height: 80vh;
    /* padding:4px ; */
    /* margin: 10px auto; */
    /* border-radius: 5px; */
    /* box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.1); */
    /* background-color: #fefefe; */

}
.services-Detail img{
    height: 300px;
    width: 100%;
    border-radius: 5px;
}
.card-info  h4{
    cursor: pointer;
    background-color: rgba(7, 56, 236, 0.681);
    width: fit-content;
    padding: 4px;
    border-radius: 5px;
    color: #f2f2f2;
    margin-bottom: 10px;
}
.card-info p{
    margin: 10px 0px;
}
.learning{
    text-decoration: none;
    margin: 20px 0px;
    font-size: 16px;
}

.service-Details{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
}

.service-Details a:first-child{
    margin: 20px;
}

@media screen  and (max-width:768px){
    .service-Details{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .service-card{
        width:220px;
        margin: 5px;
    }
    
    .services-Detail{
        width: 300px;
    }
    .price{
        font-size: 12px;
    }


}


@media screen  and (max-width:480px){
    .service-Details{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .service-card{
        width:140px;
        margin: 2px;
    }
    
    .services-Detail{
        width: 300px;
    }
    .price{
        font-size: 12px;
    }


}

.search-buttons{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
}

.search-buttons button{
    margin-right: 10px;
    border: 1px solid;
    padding: 6px ;
    border-radius: 5px;
    height: 30px;
    transition: .5s;
    background-color: #fefefe;

}
.search-buttons button:hover{
    background-color: rgba(7, 56, 236, 0.681);
    color: #f2f2f2;
    border: 1px solid #111;
}

.back{
    margin: 20x 0px;
    font-size: 18px;
    text-decoration: none;
}