.card{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 160px;
}

.card .contact-me p{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(7, 56, 236, 0.681);
    padding: 4px;
    height: 32px;
    margin-top: 10px;
    color: white;
    border-radius: 5px;
}
.my-card .contact-me a{
    text-decoration: none;
    color: white;
    font-size: 18px;
}

.my-card{
    display: flex;
    border-radius: 5px;
    justify-content: center;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3),0px 3px 3px rgba(0, 0, 0, 0.3);
    align-items: center;
    height: fit-content;
    margin: 10px 60px 10px 10px;
    background-color:  #fefefe; 
    width: fit-content;
    padding: 10px;
    border: 8px solid  rgba(7, 56, 236, 0.681);

}
.my-card h2{
    margin: 0px 0px 10px 0px;
}
.my-card img{
    height: 260px;
    width: 180px;
    border-radius: 5px;
}
.my-card li{
    list-style: circle;
}
.my-card li::marker{
    color: rgba(7, 56, 236, 0.681);
    font-weight: bolder;
    font-size: 30px;
}
.my-card .my-info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.my-card .contact-me{
    margin-top: 20px;
}


.photo{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    /* border: 6px solid rgba(0, 0, 0, 0.4); */
    width: 280px;
    padding: 10px;
    margin-right: 20px;

}

@media screen and (max-width:768px){
    .my-card {
        flex-direction: column;
        margin: 10px auto;
        width:fit-content;
        padding: 10px;
    }
    .my-card img{
        width: 100%;
        height: 300px;
    }
    .card{
        flex-direction: column;
        align-items: center;
    }
}