
.tech-card{
    display: flex;
    align-items: center;
    border-radius: 5px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.4);
     padding: 10px 20px;
    width: fit-content;
    height: 120px;
    background-color: hsla(0, 0%, 100%, 0.564);
    margin: 10px 0px;
}

.tech-card a{
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;

}
.tech-card img{
    height: 100px;
    width: 100px;
    border-radius: 5px;
    margin-right: 10px;
}
.tech-label{
    margin-left: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    line-height: 30px;
}

.tech-element{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.technologies{
    margin-top: 160px;
    display: flex;
    /* height: 80vh; */
    justify-content: center;
}

.training{
    margin-left: 20px;
}

@media screen and (max-width:768px) {
    .tech{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: auto;
    }
    .tech-card{
        width: 300px;
    }
    
}
@media screen and (max-width:768px) {
   
    .tech-label p{
        font-size: 14px;
    }
}

