.shop{
    display: flex;
    flex-direction: column;
    margin-top: 160px;
    height: 80vh;
}

.shop-hero img{
    height: 140px;
    width: 100%;
}

.shop-navBar{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0px;
}
.shop-navBar ul{
    display: flex;
}
.shop-navBar ul li {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid;
    list-style: none;
    margin-right: 10px;
    background-color: #fefefe;
    height: 30px;
}
.shop-navBar li a{
    text-decoration: none;
    color: #111;
}

@media screen  and (max-width:768px){
    .shop-navBar li a{
        font-size: 14px;
        padding: 4px;
    }
    }